.catalog-filter {
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: var(--border-radius);
  height: 5rem;
  background: var(--c-light);
}

.catalog-filter__category-button.is-active {
  background: var(--c-accent);

}

.catalog-filter__category-button {
  font-size: 1.1rem;
}